<template>
  <section>
    <v-container fluid>
      <v-row>
        <v-col md="12">
          <PageHeader :title="headercontent.title" />
        </v-col>
        <v-col md="8" v-if="!loadingData">
          <v-form
            ref="form"
            v-model="valid"
            lazy-validation
            class="form-section"
          >
            <v-row>
              <v-col cols="12">
                <v-textarea
                  v-model="message"
                  outlined
                  label="Message"
                  class="mr-btm-minus"
                ></v-textarea>
              </v-col>
              <v-col cols="12">
                <div class="mb-1">Select Emotion For Reflection Video Only</div>
                <v-select
                  v-model="categoryHandle"
                  :items="allCategory.results"
                  label="Emotion"
                  item-text="name"
                  item-value="id"
                  outlined
                ></v-select>
              </v-col>

              <v-col cols="6" v-if="false">
                <v-select
                  v-model="status"
                  :items="statuses"
                  label="Status"
                  outlined
                  item-text="name"
                  item-value="status"
                ></v-select>
              </v-col>

              <v-col cols="12" class="submitbtn">
                <v-btn
                  :disabled="!valid"
                  class="submitbutton btn-dark"
                  @click="validate"
                  :loading="loading"
                >
                  Add {{ headercontent.title }}
                </v-btn>
              </v-col>
            </v-row>
          </v-form>
        </v-col>
        <v-col md="8" v-if="loadingData">
          <v-progress-circular
            indeterminate
            color="gray"
            class="mr-3"
          ></v-progress-circular>
        </v-col>
      </v-row>
    </v-container>
  </section>
</template>

<script>
import PageHeader from "@/components/dashboard/PageHeader";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "AddPrompt",
  components: { PageHeader },
  data: () => ({
    valid: true,
    items: ["Foo", "Bar", "Fizz", "Buzz"],
    msg: "Vue Image Upload and Resize Demo",
    hasImage: false,
    image: null,
    message: "",
    description: "",
    categoryHandle: "",
    sort: 0,
    headercontent: {
      title: "Prompter",
      addlink: "/dashboard/subemotion/add",
      downloadlink: true,
    },
    content: "<h2>I am Example</h2>",
    editorOption: {
      // Some Quill options...
    },
    titleRules: [(v) => !!v || "Category Title is required"],
    statusRules: [(v) => !!v || "Status is required"],
    sortRules: {
      required: (value) => !!value || "Sort is required.",
      nan: (v) => !isNaN(v) || "Sort should be number",
    },
    descriptionRules: [(v) => !!v || "Description is required"],
    statuses: [
      { name: "Yes", status: true },
      { name: "No", status: false },
    ],
    status: true,
    imageUrl: null,
    loading: false,
    loadingData: true,
  }),
  methods: {
    removeImage() {
      this.imageUrl = "";
    },
    async validate() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        let fd = new FormData();
        fd.append("message", this.message);
        fd.append("status", this.status);
        fd.append("connectedTo", this.categoryHandle);

        if (this.$route.params.slug == undefined) {
          await this.postPrompter(fd);
        } else {
          fd.append("_id", this.id);
          await this.postPrompter(fd);
        }
        this.loading = false;
        this.$router.push("/dashboard/prompter");
      }
    },

    ...mapActions([
      "fetchCategory",
      "postPrompter",
      "fetchSubCategoryBySlug",
      "getPrompter",
    ]),
  },
  computed: {
    ...mapGetters(["allLoadingCurd", "allCategory", "allPrompter"]),
    editor() {
      return this.$refs.myQuillEditor.quill;
    },
  },

  mounted() {},

  async created() {
    await this.fetchCategory();

    if (this.$route.params.id != undefined) {
      await this.getPrompter(this.$route.params.id);
      this.loadingData = false;
      this.message = this.allPrompter.message;
      this.connectedTo = this.allPrompter.connectedTo;
      this.id = this.allPrompter._id;
    }
    this.loadingData = false;
  },
};
</script>

